body {
  font-family: Helvetica, Arial, sans-serif;
  padding: 60px;
}
button {
  padding: 8px 10px;
}
input {
  width: 100%;
  font-size: 1.2rem;
  color: gray;
}
.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  max-width: 40%;
}
.card p {
  padding: 5px;
  text-align: center;
}
.card img {
  max-height: 170px;
}
